body {
  font: 14px "Century Gothic", Futura, sans-serif;
  height: 100vh;
  margin: 20px;
  background: linear-gradient(194deg, rgba(212,138,121,0.61) 0%, rgba(218,233,174,0.61) 100%) no-repeat;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-family: 'Codystar', cursive;
  font-size: 36px;
  font-weight: bold;
  line-height: 34px;
  height: 68px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 68px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin: auto;
}

.game-info div {
  font-family: 'Press Start 2P', cursive;
  font-size: 1.75rem;
}
